@keyframes blurAnim {
  0% {
    filter: blur(0px); }
  100% {
    filter: blur(2px); } }

@keyframes rotate {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

@keyframes gradientAnim {
  0% {
    background-position: 0; }
  50% {
    background-position: 0 250px; }
  100% {
    background-position: 0; } }

@font-face {
