.nav {
  display: flex;
  gap: 70px;
  align-items: center;
  color: #000; }
  .nav__reveal {
    display: flex;
    opacity: 1;
    left: 0 !important; }
  .nav__list {
    flex-wrap: wrap;
    display: flex;
    align-items: center;
    gap: 50px; }
    @media screen and (max-width: 926px) {
