        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 70px; } }
  .nav__item {
    font-size: 1.25em; }
    .nav__item {
      position: relative; }
    .nav__item::after {
      content: '';
      position: absolute;
      bottom: -5px;
      left: 0;
      width: 0%;
      height: 2px;
      background-color: black;
      transition: 0.2s linear; }
      @media screen and (max-width: 926px) {
        .nav__item::after {
          bottom: -15px; } }
    .nav__item:hover::after {
      width: 100%; }
    @media screen and (max-width: 926px) {
      .nav__item {
        font-size: 1.625em; } }
  @media screen and (max-width: 926px) {
    .nav {
      flex-direction: column;
      align-items: center;
      position: fixed;
      top: -20px;
      left: -100vh;
      width: 100%;
      height: calc(100vh + 20px);
      z-index: 200;
